
import { Component, Vue } from 'vue-property-decorator';
import ApiService from '@/services/api.service';

import { ValidationObserver, ValidationProvider } from 'vee-validate';

@Component({
  components: {
    ValidationProvider,
    ValidationObserver,
  },
})
export default class RegisterView extends Vue {
  private apiService: ApiService = new ApiService();
  private loading = false;

  private email = '';
  private password = '';
  private sex = '';
  private username = '';
  private birthdate: Date | string = '';
  private height: number | null = null;
  private currentWeight: number | null = null;
  private goalWeight: number | null = null;

  private dismissSecs = 10;
  private dismissCountDown = 0;
  private showDismissibleAlert = false;

  validationState({
    dirty,
    validated,
    valid,
  }: {
    dirty: boolean;
    validated: boolean;
    valid: boolean;
  }) {
    return dirty || validated ? valid : null;
  }

  countDownChanged(dismissCountDown: number) {
    this.dismissCountDown = dismissCountDown;
  }

  showAlert() {
    this.dismissCountDown = this.dismissSecs;
  }

  get sexOptions() {
    return [
      { label: 'Male', value: 'male' },
      { label: 'Female', value: 'female' },
    ];
  }

  async register() {
    // console.log('register');

    this.loading = true;

    try {
      const response = await this.apiService.register({
        password: this.password,
        email: this.email,
        sex: this.sex,
        username: this.username,
        birthdate: this.birthdate,
        height: this.height,
        currentWeight: this.currentWeight,
        goalWeight: this.goalWeight,
      });

      if (response) {
        this.$router.push({ name: 'login' });
      }
    } catch (error) {
      console.log(error);
      this.showAlert();
    }

    this.loading = false;
  }

  mounted() {
    const user = localStorage.getItem('user');
    if (user) {
      this.$router.push({ name: 'dashboard' });
    }
  }
}
